  },
  mounted () {
      $('.right-fixed').fadeIn();
    /**/
    $('#dowebok').fullpage({
        navigationColor: '#ff0000',
        sectionsColor: ['#f6f6f6', '#f6f6f6', '#ffffff', '#ffffff', '#ffffff', '#f6f6f6'],
        navigation: false,
        afterLoad:function(index,nextIndex,direction){
            //console.log(nextIndex);
            if(nextIndex==1 || nextIndex==4){
                $('.nav').removeClass('black');
            }else{
                $('.nav').addClass('black');
            }
        },
        onLeave: function(index, nextIndex, direction){
            var $animated=$('.section').eq(index -1).find('.animated'),
                animated_name=$animated.attr('data-animated');
            $('.section').eq(index -1).removeClass('fadeIn');
            $animated.removeClass(animated_name).hide(0);
            //console.log(animated_name);
            setTimeout(function(){
                var $animated=$('.section').eq(nextIndex -1).find('.animated'),
                    animated_name=$animated.attr('data-animated');
                $('.section').eq(nextIndex -1).addClass('fadeIn');
                $animated.show(0).addClass(animated_name);
            },400);
            
        }
    });
    /**/
    $('.nav .dh>li').hover(function(){
        var $this=$(this),
            cur_num=$this.prevAll().length;
        $('.nav .dh').css({
            'background-position': (cur_num*100)+'px 0'
        });    
    },function(){
        $('.nav .dh').css({
            'background-position': '0 0'
        }); 
    });
  }
}
</script>

